.logo {
    height: 36px;
    width: 36px;
    background: url(/static/media/logo.5ea563af.svg) no-repeat center center;
    background-size: 80%;
    border-radius: 6px;
    margin: 6px;
    display: inline-block;
}

.logo-text {
    float: right;
    line-height: 50px;
    color: #fff;
    margin-right: 50px;
    font-size: 18px;
}

.col-span {
    margin-right: 15px;
}

.col-hidden {
    display: none;
}

.login-logo {
    height: 70px;
    width: 70px;
    background: url(/static/media/logo.5ea563af.svg) no-repeat center center;
    background-size: 100%;
    border-radius: 6px;
    display: inline-block;
    float: left;
}

.login-logo-text {
    color: #20311e;
    font-size: 20px;
    display: inline-block;
    float: left;
    height: 70px;
    line-height: 70px;
    margin-left: 20px;
}

.base_png {
    height: 80%;
    background: url(/static/media/logo.5ea563af.svg) no-repeat center;
    background-size: 20%;
}

.ip_column {
    padding: 0 !important;
    font-size: 12px;
}

.ant-carousel .slick-dots li button {
    background: #9ba2bd !important;
}

.ant-carousel .slick-dots li.slick-active button {
    background: #40a9ff !important;
}

.CodeMirror {
    background-color: #ddffff !important;
    padding: 10px;
    border-left: 6px solid #ccc !important;
    border-color: #2196f3 !important;
}

.wechat {
    height: 60px;
    width: 60px;
    background: url(/static/media/wechat.e5b68bf8.jpeg) no-repeat center center;
    background-size: 100%;
    border-radius: 3px;
    display: inline-block;
}

.login-ad {
    width: 100%;
    height: 100%;
    background: url(/static/media/login.885dc68b.svg) no-repeat center center;
    background-size: 60%;
    border-radius: 6px;
    display: inline-block;
}

.welcome-ad {
    width: 100%;
    height: 100%;
    background: url(/static/media/welcome.acf1332f.svg) no-repeat center center;
    background-size: 60%;
    border-radius: 6px;
    display: inline-block;
}

@keyframes fade {
    from {
        background-color: #dfefff;
    }
    50% {
        background-color: #ffffff;
    }
    to {
        background-color: #dfefff;
    }
}

@-webkit-keyframes fade {
    from {
        background-color: #dfefff;
    }
    50% {
        background-color: #ffffff;
    }
    to {
        background-color: #dfefff;
    }
}

.flashing {
    color: #06f;
    animation: fade 700ms infinite;
    -webkit-animation: fade 700ms infinite;
}

.jenkinsEditor {
    font-size: 11px;
}

.preJenkinsLog {
    font-size: 12px;
    height: calc(100% - 30px);
    margin-left: -30px;
    margin-right: -30px;
    overflow: auto;
    flex: 1 1;
    padding-bottom: 300px;
    background-color: #202d40 !important;
    color: #f5f5f5;
    padding: 0.5rem 0;
    font-size: 12px;
    font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, Courier,
        monospace !important;
    margin: 0;
    white-space: pre-wrap;
}

.steps-content {
    margin-top: 16px;
    border: 1px dashed #e9e9e9;
    border-radius: 6px;
    background-color: #fafafa;
    min-height: 200px;
    text-align: center;
    padding-top: 80px;
}
.CodeMirror {
    background-color: #ddffff !important;
    padding: 10px;
    border-left: 6px solid #ccc !important;
    border-color: #2196f3 !important;
    height: 100%;
}

.sqlEditor {
    width: 100%;
}

.react-codemirror2 {
    height: 100%;
}

.ant-table-pagination.ant-pagination {
    margin: 5px 0px;
}


#components-form-demo-normal-login .login-form {
    max-width: 300px;
}
#components-form-demo-normal-login .login-form-forgot {
    float: right;
}
#components-form-demo-normal-login .login-form-button {
    width: 100%;
    height: 45px;
}

